import React from "react";
import { FirebaseDatabaseProvider } from "@react-firebase/database";
import PropTypes from "prop-types";

import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { connect } from "react-redux";

import CssBaseline from "@material-ui/core/CssBaseline";
import { makeStyles } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";

import MyTheme from "../MyTheme";
import MyAppbar from "./MyAppbar";
import MyDrawer from "./MyDrawer";
import Home from "./home";
import Statistics from "./statistics";
import Info from "./info";
import Settings from "./settings";
import About from "./about";
import ProtectedRoute from "./ProtectedRoute";
import Login from "./login";
import Timer from "./timerlogic";

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1,
    height: "100%"
  }
}));

App.propTypes = {
  isAuthenticated: PropTypes.bool,
  isVerifying: PropTypes.bool
};

//this is the App component which includes all the other components and router for linking all subpages
function App(props) {
  const classes = useStyles();
  const { isAuthenticated, isVerifying } = props;

  return (
    <React.Fragment>
      <CssBaseline />
      <FirebaseDatabaseProvider>
        <ThemeProvider theme={MyTheme}>
          <Router basename="/">
            <div className={classes.root}>
              <MyAppbar></MyAppbar>
              <MyDrawer></MyDrawer>
              <Timer></Timer>
              <Switch>
                <ProtectedRoute
                  exact
                  path="/"
                  component={Home}
                  isAuthenticated={isAuthenticated}
                  isVerifying={isVerifying}
                />
                <Route path="/login" component={Login} />
                <Route path="/statistics/" component={Statistics} />
                <Route path="/info/" component={Info} />
                <Route path="/settings/" component={Settings} />
                <Route path="/about/" component={About} />
              </Switch>
            </div>
          </Router>
        </ThemeProvider>
      </FirebaseDatabaseProvider>
    </React.Fragment>
  );
}

//mapping the state from the redux store to the props to check if user is authenticated
function mapStateToProps(state) {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    isVerifying: state.auth.isVerifying
  };
}

export default connect(mapStateToProps)(App);
