import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InfoIcon from "@material-ui/icons/Info";
import HomeIcon from "@material-ui/icons/Home";
import RestoreIcon from "@material-ui/icons/Restore";
import SchoolIcon from "@material-ui/icons/School";
import SettingsIcon from "@material-ui/icons/Settings";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";

//styling for the list
const useStyles = makeStyles({
  list: {
    width: 250
  }
});

//this is the Drawer component
function MyDrawer() {
  const classes = useStyles();
  const dispatch = useDispatch();
  //getting boolean from redux store if drawer is open
  const drawerOpen = useSelector(state => {
    return state.navDrawerOpen;
  });

  return (
    <Drawer
      open={drawerOpen}
      onClose={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
    >
      <div className={classes.list} role="presentation">
        <List>
          <ListItem
            button
            component={Link}
            to="/"
            onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
          >
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary="Home" />
          </ListItem>

          <ListItem
            button
            component={Link}
            to="/statistics"
            onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
          >
            <ListItemIcon>
              <RestoreIcon />
            </ListItemIcon>
            <ListItemText primary="Statistics" />
          </ListItem>

          <ListItem
            button
            component={Link}
            to="/info"
            onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
          >
            <ListItemIcon>
              <SchoolIcon />
            </ListItemIcon>
            <ListItemText primary="What is pomodoro?" />
          </ListItem>

          <Divider />

          <ListItem
            button
            component={Link}
            to="/settings"
            onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
          >
            <ListItemIcon>
              <SettingsIcon />
            </ListItemIcon>
            <ListItemText primary="Settings" />
          </ListItem>

          <ListItem
            button
            component={Link}
            to="/about"
            onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: false })}
          >
            <ListItemIcon>
              <InfoIcon />
            </ListItemIcon>
            <ListItemText primary="About" />
          </ListItem>
        </List>
      </div>
    </Drawer>
  );
}

export default MyDrawer;
