import React, { Fragment, useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector } from "react-redux";
import List from "@material-ui/core/List";
import { db } from "../firebaseConfig";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import LinearProgress from "@material-ui/core/LinearProgress";
import { inherits } from "util";

let userId;

//styling for the header and root
const useStyles = makeStyles(theme => ({
  header: {
    marginBottom: 20,
    marginLeft: 30,
    marginTop: 40
  },
  root: {
    flexGrow: 1,
    margin: 20
  }
}));

//this is a row component; each task from the database is a presented in a row
function Row(props) {
  const { title, description, startTime, style } = props;

  return (
    <Fragment>
      <Paper
        style={{
          ...style,
          left: 20,
          padding: 10,
          margin: 20,
          width: "98%",
          height: inherits
        }}
      >
        <Typography variant="h9" component="h3">
          {title}
        </Typography>
        <Typography component="p">{description}</Typography>
        <Typography component="p" align="right">
          {startTime}
        </Typography>
      </Paper>
    </Fragment>
  );
}

//this is the statistics component
export default function Statistics() {
  const classes = useStyles();
  //grabbing the user id from the redux store
  userId = useSelector(state => {
    return state.auth.user.uid;
  });

  const [tasks, setTasks] = useState([]);

  //firestore query and snapshot with the task infos
  useEffect(() => {
    const fetchData = async () => {
      const query = db
        .collection(`TaskInfos/${userId}/Tasks`)
        .orderBy("startTime", "asc")
        .startAt(0);
      const snapshot = await query.get();
      setTasks(snapshot.docs.reverse());
    };

    fetchData();
  }, []);

  return (
    <Fragment>
      <Typography
        className={classes.header}
        component="p"
        gutterBottom={true}
        variant="h5"
      >
        Here you can find an overview of your past Pomodori.
      </Typography>
      {tasks.length === 0 ? (
        <div className={classes.root}>
          <LinearProgress />
        </div>
      ) : (
        <List>
          {tasks.map(task => (
            <Row
              key={task.id}
              title={task.data().title}
              description={task.data().description}
              startTime={new Date(task.data().startTime).toLocaleString()}
            ></Row>
          ))}
        </List>
      )}
    </Fragment>
  );
}
