import { combineReducers } from "redux";
import auth from "./auth";
import timer from "./timer";

import navDrawerReducer from "./navDrawerReducer";

//this is the root reducer, which bundles all other reducer into one, which is passed to the store in configureStore.js
export default combineReducers({
  navDrawerOpen: navDrawerReducer,
  auth,
  timer
});
