import {
  SET_SETTINGS,
  TIMER_STARTED,
  TIMER_RUN,
  TIMER_CANCLED,
  TIMER_PAUSE,
  TIMER_PROMODORO_DONE,
  CURRENT_TIME,
  SET_TASK_TITLE,
  SET_TASK_DESC,
  TIMER_COOLDOWN,
  TIMER_COOLDOWN_DISABLED
} from "../actions/timer";

export default (
  state = {
    timeSetting: 25,
    currentTime: 0,
    promodori: 0,
    isStarted: false,
    isPaused: true,
    coolDown: false,
    taskTitle: "",
    taskDesc: ""
  },
  action
) => {
  switch (action.type) {
    case SET_SETTINGS:
      return {
        ...state,
        timeSetting: action.payload
      };
    case TIMER_STARTED:
      return {
        ...state,
        isStarted: true,
        isPaused: false
      };
    case TIMER_RUN:
      console.log("let it run");
      return {
        ...state,
        isPaused: false
      };
    case TIMER_PAUSE:
      console.log("let it pause");
      return {
        ...state,
        isPaused: true
      };
    case TIMER_CANCLED:
      return {
        ...state,
        isStarted: false
      };
    case CURRENT_TIME:
      return {
        ...state,
        currentTime: action.payload
      };
    case TIMER_PROMODORO_DONE:
      return {
        ...state,
        promodori: action.payload
      };
    case TIMER_COOLDOWN:
      return {
        ...state,
        coolDown: true
      };
    case TIMER_COOLDOWN_DISABLED:
      return {
        ...state,
        coolDown: false
      };
    case SET_TASK_TITLE:
      return {
        ...state,
        taskTitle: action.payload
      };
    case SET_TASK_DESC:
      return {
        ...state,
        taskDesc: action.payload
      };
    default:
      return state;
  }
};
