import React from "react";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Fab from "@material-ui/core/Fab";
import makeStyles from "@material-ui/core/styles/makeStyles";
import { useDispatch } from "react-redux";
import { withStyles } from "@material-ui/styles";
import PlayArrowRoundedIcon from "@material-ui/icons/PlayArrowRounded";
import Logo from "../LogoPromodori/svgs/Logo_geteilt0.svg";
import TextField from "@material-ui/core/TextField";
import { useSelector } from "react-redux";
import {
  timerPause,
  timerRun,
  timerStart,
  setTaskTitle,
  setTaskDesc,
  setCurrentTrigger
} from "../actions/timer";
import firebase from "firebase/app";
import "firebase/functions";

import PauseRoundedIcon from "@material-ui/icons/PauseRounded";

import CustomizedProgressBars from "./ProgressBar";

const styles = makeStyles({
  "@global": {
    body: {
      backgroundColor: "#fff"
    }
  },
  paper: {
    marginTop: 100,
    display: "flex",
    padding: 20,
    flexDirection: "column",
    alignItems: "center",
    overflow: "hidden"
  },
  avatar: {
    marginLeft: "auto",
    marginRight: "auto",
    backgroundColor: "#f50057"
  },
  form: {
    marginTop: 1
  },
  errorText: {
    color: "#f50057",
    marginBottom: 5,
    textAlign: "center"
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  }
});

function Home() {
  const classes = styles();
  const startNewTask = firebase
    .app()
    .functions("europe-west2")
    .httpsCallable("startTask");
  const toggleTask = firebase
    .app()
    .functions("europe-west2")
    .httpsCallable("toggleTask");
  const dispatch = useDispatch();
  const taskDuration = useSelector(state => {
    return state.timer.timeSetting;
  });
  const currentTime = useSelector(state => {
    return state.timer.currentTime;
  });
  const secInString = () => {
    let minutes = Math.floor(currentTime / 60);
    let seconds = Math.floor(currentTime % 60);
    return minutes + ":" + seconds;
  };
  const time = secInString();
  const isStarted = useSelector(state => {
    return state.timer.isStarted;
  });
  const task = useSelector(state => {
    return state.timer.taskTitle;
  });
  const taskDesc = useSelector(state => {
    return state.timer.taskDesc;
  });
  const isPaused = useSelector(state => {
    return state.timer.isPaused;
  });
  const CheckInputTask = () => {
    let input = document.getElementById("edit_Task").valueOf().value;
    dispatch(setTaskTitle(input));
  };
  const CheckInputDesc = () => {
    let input = document.getElementById("edit_Taskdescription").valueOf().value;
    dispatch(setTaskDesc(input));
  };
  const togglePlayPause = () => {
    if (!isStarted) {
      if (!("" === task) && !("" === taskDesc)) {
        dispatch(setCurrentTrigger(taskDuration * 60));
        dispatch(timerStart());
        startNewTask({
          taskTitle: task,
          taskDescription: taskDesc,
          taskDuration: taskDuration
        })
          .then(result => {
            console.log(JSON.stringify("Task created!"));
          })
          .catch(error => {
            console.log(error);
          });
      }
    } else {
      if (isPaused) {
        dispatch(timerRun());
        toggleTask({})
          .then(result => {
            console.log(JSON.stringify("Task toggled!"));
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        dispatch(timerPause());
        toggleTask({})
          .then(result => {
            console.log(JSON.stringify("Task toggled!"));
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Paper
        className={classes.paper}
        style={{
          width: "inherit"
        }}
      >
        <li
          className="mdc-image-list__item"
          style={{
            width: "inherit"
          }}
        >
          <div
            className="mdc-image-list__image-aspect-container"
            style={{
              width: "inherit"
            }}
          >
            <img
              alt=""
              className="mdc-image-list__image"
              src={Logo}
              style={{
                width: "inherit"
              }}
            />
          </div>
          <div className="mdc-card">
            <div className="mdc-card__primary-action" tabIndex="0">
              <Typography
                id="TimerView"
                component="h1"
                variant="h2"
                style={{
                  position: "absolute",
                  color: "white",
                  fontWeight: "bold",
                  top: 330,
                  left: "50%",
                  transform: "translateX(-50%)"
                }}
              >
                {time}
              </Typography>
            </div>
          </div>
        </li>
        <CustomizedProgressBars />
        <TextField
          id="edit_Task"
          disabled={isStarted}
          className={classes.textField}
          onChange={CheckInputTask}
          placeholder="Task"
          margin="normal"
          component="h4"
          inputProps={{ "aria-label": "Task" }}
        />
        <TextField
          id="edit_Taskdescription"
          disabled={isStarted}
          className={classes.textField}
          onChange={CheckInputDesc}
          placeholder="Taskdescription"
          margin="normal"
          inputProps={{ "aria-label": "Taskdescription" }}
        />
        <Fab
          id="pauseButton"
          disabled={useSelector(state => {
            return state.timer.isPaused;
          })}
          onClick={togglePlayPause}
          style={{ marginTop: 10 }}
          size="large"
          variant="round"
          color="primary"
        >
          <PauseRoundedIcon fontSize={"large"} />
        </Fab>
        <Fab
          id="playButton"
          disabled={
            !useSelector(state => {
              return state.timer.isPaused;
            })
          }
          onClick={togglePlayPause}
          style={{ marginTop: 10 }}
          size="large"
          variant="round"
          color="primary"
        >
          <PlayArrowRoundedIcon fontSize={"large"} />
        </Fab>
      </Paper>
    </Container>
  );
}

/*export default Home;
 */

export default withStyles(styles)(Home);
