import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import ListSubheader from "@material-ui/core/ListSubheader";
import Switch from "@material-ui/core/Switch";
import SpeakerIcon from "@material-ui/icons/Speaker";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import Paper from "@material-ui/core/Paper";
import { setSettings, wantSound } from "../actions/timer";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";

// Styling
const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    maxWidth: 500,
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1)
    },
    dense: {
      marginTop: theme.spacing(2)
    },
    iconSmall: {
      fontSize: 20
    },
    button: {
      margin: theme.spacing(1)
    }
  }
}));

const marks = [
  {
    value: 10,
    label: "10 Min"
  },
  {
    value: 25,
    label: "25 Min"
  },
  {
    value: 50,
    label: "50 Min"
  },
  {
    value: 75,
    label: "75 Min"
  },
  {
    value: 100,
    label: "100 Min"
  }
];

function valuetext(value) {
  return `${value}`;
}

export default function SettingsList() {
  const dispatch = useDispatch();
  // Reads existing values from state
  let pomodoroLength = useSelector(state => {
    return state.timer.timeSetting;
  });
  let userWantSound = useSelector(state => {
    return state.timer.wantSound;
  });
  // Checks if the timer is already running to determine if changing those values is even possible.
  const isStarted = useSelector(state => {
    return state.timer.isStarted;
  });
  const classes = useStyles();
  const [checked, setChecked] = React.useState([userSound()]);

  function userSound() {
    const yes = "sound";
    const no = "noTicking";
    if (userWantSound) {
      return yes;
    } else {
      return no;
    }
  }

  const handleToggle = value => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    if (newChecked[0]) {
      dispatch(wantSound(true));
    } else {
      dispatch(wantSound(false));
    }

    setChecked(newChecked);
  };

  return (
    <Paper
      style={{
        left: 20,
        padding: 10,
        margin: 20,
        width: "98%"
      }}
    >
      <div>
        <List
          subheader={<ListSubheader>Settings</ListSubheader>}
          className={classes.root}
        >
          <ListItem>
            <div className={classes.margin} />
            <Typography id="pomodoroSlider" gutterBottom>
              Length of one Pomodoro
            </Typography>
            <Slider
              defaultValue={pomodoroLength}
              disabled={isStarted}
              getAriaValueText={valuetext}
              // Dispatches settings to store on data change
              onChange={(e, value) => {
                dispatch(setSettings(value));
              }}
              aria-labelledby="pomodoroSlider"
              min={5}
              step={5}
              marks={marks}
              valueLabelDisplay="on"
            />
          </ListItem>
          <ListItem>
            <ListItemIcon>
              <SpeakerIcon />
            </ListItemIcon>
            <ListItemText
              id="switch-list-label-sound"
              primary="Ticking sound"
            />
            <ListItemSecondaryAction>
              <Switch
                edge="end"
                onChange={handleToggle("sound")}
                checked={checked.indexOf("sound") !== -1}
                disabled={isStarted}
                inputProps={{ "aria-labelledby": "switch-list-label-sound" }}
              />
            </ListItemSecondaryAction>
          </ListItem>
        </List>
      </div>
    </Paper>
  );
}
