import React from "react";
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Paper from "@material-ui/core/Paper";



const useStyles = makeStyles({
  root: {
    width: '100%',

  },
});

export default function Info() {
  const classes = useStyles();

  return (
      <Paper
          style={{
              left: 20,
              padding: 10,
              margin: 20,
              width: "98%",
          }}
      >
      <div className={classes.root}>
        <Typography variant="h5" gutterBottom>
          What is the Pomodoro Technique?
        </Typography>
        <Typography variant="body1" gutterBottom>
          The Pomodoro Technique is a time management method developed by Francesco Cirillo in the late 1980s.
          The technique uses a timer to break down work into intervals, traditionally 25 minutes in length, separated by
          short breaks. Each interval is known as a pomodoro, from the Italian word for "tomato", after the
          tomato-shaped kitchen timer that Cirillo used as a university student. There are six steps in the original
          technique:
        </Typography>
        <List>
              <ListItem>
                <ListItemText
                    primary="1. Decide on the task to be done."
                />
              </ListItem>
              <ListItem>
                <ListItemText
                    primary="2. Set the pomodoro timer (traditionally to 25 minutes)."
                />
              </ListItem>
              <ListItem>
                <ListItemText
                    primary="3. Work on the task."
                />
              </ListItem>
              <ListItem>
                <ListItemText
                    primary="4. End work when the timer rings and put a checkmark on a piece of paper."
                />
              </ListItem>
              <ListItem>
                <ListItemText
                    primary="5. If you have fewer than four checkmarks, take a short break (3–5 minutes), then go to step 2."
                />
              </ListItem>
              <ListItem>
                <ListItemText
                    primary="6. After four pomodoros, take a longer break (15–30 minutes), reset your checkmark count to zero, then go to step 1."
                />
              </ListItem>
        </List>
        <Typography variant="body1" gutterBottom>
          -Wikipedia
        </Typography>

      </div>
      </Paper>
  );
}
