import { useSelector } from "react-redux";
import {
  setCurrentTrigger,
  timerPromodoroDone,
  timerCoolDownModeDisable,
  timerCoolDownMode,
  timerCancled
} from "../actions/timer";
import { useDispatch } from "react-redux";

function Timer() {
  const dispatch = useDispatch();
  const isStarted = useSelector(state => {
    return state.timer.isStarted;
  });
  const isPaused = useSelector(state => {
    return state.timer.isPaused;
  });
  const coolDown = useSelector(state => {
    return state.timer.coolDown;
  });
  const currentTime = useSelector(state => {
    return state.timer.currentTime;
  });
  const currentPromodori = useSelector(state => {
    return state.timer.promodori;
  });
  setInterval(function() {
    if (new Date().getTime() % 1000 === 0) {
      if (isStarted) {
        if (!isPaused) {
          dispatch(setCurrentTrigger(currentTime - 1));
          if (currentTime - 1 <= 0) {
            if (coolDown) {
              dispatch(timerCoolDownModeDisable());
              dispatch(timerCancled());
              alert("PAUSE FINISHED");
            } else {
              dispatch(timerCoolDownMode());
              dispatch(setCurrentTrigger(5 * 60));
              if (currentPromodori >= 4) {
                dispatch(timerPromodoroDone(0));
              } else {
                dispatch(timerPromodoroDone(currentPromodori + 1));
              }
              alert("PROMODORO FINISHED");
            }
          }
          dispatch(setCurrentTrigger(currentTime));
        }
      }
    }
  }, 1000);
  return null;
}
export default Timer;
