import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import AccountCircle from "@material-ui/icons/AccountCircle";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { logoutUser } from "../actions/auth";

//styling for menuButton and title of the app
const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2)
  },
  title: {
    flexGrow: 1
  }
}));

//this is the AppBar component
function MyAppbar() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  //grabbing boolean from redux store if user is authenticated
  const userLoggedIn = useSelector(state => {
    return state.auth.isAuthenticated;
  });

  //function for opening drawer
  const handleMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  //function for closing drawer
  const handleClose = () => {
    setAnchorEl(null);
  };

  //function for handling a logout; dispatching a logout action to update the redux store
  const handleLogout = () => {
    setAnchorEl(null);
    dispatch(logoutUser());
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <IconButton
          disabled={!userLoggedIn}
          onClick={() => dispatch({ type: "TOGGLE_DRAWER", state: true })}
          edge="start"
          className={classes.menuButton}
          color="inherit"
          aria-label="menu"
        >
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" className={classes.title}>
          KetchupTime
        </Typography>

        <div>
          <IconButton
            disabled={!userLoggedIn}
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right"
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right"
            }}
            open={open}
            onClose={handleClose}
          >
            <MenuItem component={Link} to="/login" onClick={handleLogout}>
              Logout
            </MenuItem>
          </Menu>
        </div>
      </Toolbar>
    </AppBar>
  );
}
export default MyAppbar;
