export const SET_SETTINGS = "SET_SETTINGS";
export const TIMER_STARTED = "TIMER_STARTED";
export const TIMER_PAUSE = "TIMER_PAUSE";
export const TIMER_RUN = "TIMER_RUN";
export const TIMER_CANCLED = "TIMER_CANCLED";
export const TIMER_COOLDOWN = "TIMER_COOLDOWN";
export const TIMER_COOLDOWN_DISABLED = "TIMER_COOLDOWN_DISABLED";
export const TIMER_PROMODORO_DONE = "TIMER_PROMODORO_DONE";
export const TIMER_TRIGGERED = "TIMER_TRIGGERED";
export const TIMER_TRIGGERED_DISABLE = "TIMER_TRIGGERED_DISABLE";
export const CURRENT_TIME = "CURRENT_TIME";
export const WANT_SOUND = "WANT_SOUND";
export const SET_TASK_TITLE = "SET_TASK_TITLE";
export const SET_TASK_DESC = "SET_TASK_DESC";
export const TIMER_INIZIALISED = "TIMER_INIZIALISED";

export const setTaskTitle = Title => {
  return {
    type: SET_TASK_TITLE,
    payload: Title
  };
};

export const setTaskDesc = Desc => {
  return {
    type: SET_TASK_DESC,
    payload: Desc
  };
};

export const setSettings = settings => {
  return {
    type: SET_SETTINGS,
    payload: settings
  };
};

export const timerStart = () => {
  return {
    type: TIMER_STARTED
  };
};

export const timerRun = () => {
  return {
    type: TIMER_RUN
  };
};

export const timerPause = () => {
  return {
    type: TIMER_PAUSE
  };
};

export const wantSound = () => {
  return {
    type: WANT_SOUND
  };
};

export const timerCancled = () => {
  return {
    type: TIMER_CANCLED
  };
};

export const timerCoolDownMode = () => {
  return {
    type: TIMER_COOLDOWN
  };
};

export const timerCoolDownModeDisable = () => {
  return {
    type: TIMER_COOLDOWN_DISABLED
  };
};

export const timerPromodoroDone = promoCounter => {
  return {
    type: TIMER_PROMODORO_DONE,
    promoCounter
  };
};

export const setCurrentTrigger = timeTillEnd => {
  return {
    type: CURRENT_TIME,
    payload: timeTillEnd
  };
};

